import Vue from 'vue'

if (Vue.util.isWechat) {
  var script = document.createElement('script')
  script.setAttribute('type', 'text/javascript')
  script.setAttribute('src', 'http://res.wx.qq.com/open/js/jweixin-1.0.0.js')
  document.head.appendChild(script)
}
var weixin = {
  _config: function () {
    var wx = window.wx
    if (!wx || !wx.config) {
      return
    }
    // 
    Vue.http.get('http://192.168.1.15:1337/wechat/getParam').then(function (response) {
      console.log(response)
      var data = response.data
      var param = {
        debug: true,
        appId: data.appId,
        timestamp: data.timestamp,
        nonceStr: data.nonceStr,
        signature: data.signature,
        jsApiList: [
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
          'onMenuShareQQ',
          'onMenuShareWeibo'
        ]
      }

      // 
      wx.config(param)

      // 
      wx.ready(function () {
        // 
        wx.onMenuShareTimeline({
          title: '',
          link: '',
          imgUrl: '',
          success: function (res) {
            window.alert('')
          },
          cancel: function (res) {
            window.alert('')
          }
        })

        // // 
        // wx.onMenuShareAppMessage({
        //   title: '', // 
        //   link: '', // 
        //   imgUrl: '', // 
        //   desc: '', // 
        //   type: '', // ,musicvideolinklink
        //   dataUrl: '', // typemusicvideo
        //   trigger: function (res) {
        //     // triggerajaxajax
        //     alert('')
        //   },
        //   success: function (res) {
        //     alert('')
        //   },
        //   cancel: function (res) {
        //     alert('')
        //   },
        //   fail: function (res) {
        //     alert(JSON.stringify(res))
        //   }
        // })
      })

      // 
      window.wx.error(function (res) {
        console.log(res)
      })
    }, function (response) {
      console.log(response.data)
    })
  },
  share: function (data) {
    this._config(data)
  },
  pay: function () {

  }
}

export default weixin



